* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

body {
  height: 100%;
  background: #796a7a;
}

.container {
  background: #f7d9fa;
  width: 400px;
  padding: 35px;
  margin: 50px auto;
  border-radius: 10px;
  box-shadow: 15px 30px 35px rgba(0, 0, 0, 0.1);
}

.coin {
  height: 200px;
  width: 200px;
  position: relative;
  margin: 32px auto;
  transform-style: preserve-3d;

  & img {
    width: 100%;
  }
}

.heads,
.tails {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  z-index: 0;
}

.heads {
  transform: rotateY(180deg);
}

@keyframes spin-tails {
  0% {
    transform: rotateY(0);
  }

  100% {
    transform: rotateY(1980deg);
  }
}

@keyframes spin-heads {
  0% {
    transform: rotateY(0);
  }

  100% {
    transform: rotateY(2160deg);
  }
}

.stats {
  display: flex;
  justify-content: space-between;
  color: #fff;
  font-weight: 500;
  margin-top: 55px;
  margin-bottom: 40px;
  box-shadow: 0 0 20px rgba(0, 139, 253, 0.25);
  font-size: 22px;

  & span {
    font-weight: 700;
  }

  &__count {
    background: #796a7a;
    padding: 20px;
    width: 100%;
    text-align: center;

    &--tails {
      border-left: 2px dashed #fff;
    }
  }
}

.buttons {
  display: flex;
  justify-content: space-between;
}

button {
  width: 150px;
  padding: 15px 0;
  border: none;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;

  transition: background 400ms, color 400ms;
}

#flip-button {
  background: #618aa1;
  color: white;

  &:hover {
    background: #d9564a;
  }
}

#flip-button:disabled {
  background: #d9564a;
  box-shadow: 0 0 20px rgba(217, 102, 144, 0.25);
}

#reset-button {
  background: #4a9b93;
  color: white;
  &:hover {
    background: #d9564a;
  }
  &:focus {
    background: #d9564a;
  }
}
